import React from 'react';
import { routes, nav } from 'router';


const studentImage = require('assets/img/student-img.png');
const teacherImage = require('assets/img/teacher-img.png');


const Introduction = () => {

  const redirectToTeacher = () => nav(routes.teacherRegistration);
  const redirectToStudent = () => nav(routes.studentRegistration);

  return (
    <section className="section introduction-section bg-gray">
      <div className="container">
        <div className="row py-md-5 py-5 section-block">
          <p className="introduction-text">
                                           .
          </p>

          <div className="col-12 col-lg-4 user-segment d-flex flex-column justify-content-between align-items-center">
            <img alt="Student" className="introduction-segment-img" src={studentImage} />
            <button className="btn btn-primary btn-lg introduction-segment-btn" onClick={redirectToStudent}> </button>
          </div>

          <div className="col-12 col-lg-4 user-segment d-flex flex-column justify-content-between align-items-center">
            <img alt="Teacher" className="introduction-segment-img" src={teacherImage} />
            <button className="btn btn-secondary btn-lg introduction-segment-btn" onClick={redirectToTeacher}> </button>
          </div>

        </div>
      </div>
    </section>
  );
}


export default Introduction;
